import PFElement from "../pfelement/pfelement.js";

class PfeBadge extends PFElement {
  get html() {
    return `<style>:host {
  --pfe-badge--base--bg:           var(--pfe-theme--color--feedback--default--lightest, #ececec);
  --pfe-badge--base--color:                               var(--pfe-theme--color--text, #333);
  --pfe-badge--base--font-size:                        var(--pfe-theme--font-size, 16px);
  display: inline-block;
  padding: 0.35rem 0.8rem;
  border: 1px solid transparent;
  border-radius: 20px !important;
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important;
  font-size: calc(var(--pfe-badge--base--font-size) * 0.8);
  font-weight: var(--pfe-theme--font-weight--bold, 700);
  line-height: 1.1; }
  :host ::slotted(*) {
    display: inline-block;
    border-radius: 20px !important;
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important;
    vertical-align: middle; }

:host([size="sm"]) {
  padding: 0.3rem 0.7rem;
  font-size: calc(var(--pfe-badge--base--font-size) * 0.7);
  font-weight: var(--pfe-theme--font-weight--bold, 700); }

:host([size="md"]) {
  padding: 0.35rem 0.8rem;
  font-size: calc(var(--pfe-badge--base--font-size) * 0.8); }

:host([size="lg"]) {
  padding: 0.4rem 0.85rem;
  font-size: calc(var(--pfe-badge--base--font-size) * .9);
  line-height: 1.2; }

:host([color="white"]) {
  --pfe-badge--base--bg:                     var(--pfe-theme--color--feedback--default, #606060);
  --pfe-badge--base--color:                               var(--pfe-theme--color--text, #333);
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important; }
  :host([color="white"]) ::slotted(*) {
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important; }

:host([color="grey"]),
:host([color="gray"]),
:host([priority="default"]) {
  --pfe-badge--base--bg:                     var(--pfe-theme--color--feedback--default, #606060);
  --pfe-badge--base--color:                               var(--pfe-theme--color--text, #333);
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important; }
  :host([color="grey"]) ::slotted(*),
  :host([color="gray"]) ::slotted(*),
  :host([priority="default"]) ::slotted(*) {
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important; }

:host([color="slate"]),
:host([priority="normal"]) {
  --pfe-badge--base--bg:            var(--pfe-theme--color--feedback--default--darkest, #464646);
  --pfe-badge--base--color:                      var(--pfe-theme--color--text--on-dark, #fff);
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important;
  font-weight: var(--pfe-theme--font-weight--semi-bold, 600); }
  :host([color="slate"]) ::slotted(*),
  :host([priority="normal"]) ::slotted(*) {
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important; }

:host([color="blue"]),
:host([priority="info"]) {
  --pfe-badge--base--bg:                       var(--pfe-theme--color--feedback--info, #0277bd);
  --pfe-badge--base--color:                     var(--pfe-theme--color--text--on-dark, #fff);
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important;
  font-weight: var(--pfe-theme--font-weight--semi-bold, 600); }
  :host([color="blue"]) ::slotted(*),
  :host([priority="info"]) ::slotted(*) {
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important; }

:host([color="green"]),
:host([priority="success"]) {
  --pfe-badge--base--bg:                    var(--pfe-theme--color--feedback--success, #2e7d32);
  --pfe-badge--base--color:                     var(--pfe-theme--color--text--on-dark, #fff);
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important;
  font-weight: var(--pfe-theme--font-weight--semi-bold, 600); }
  :host([color="green"]) ::slotted(*),
  :host([priority="success"]) ::slotted(*) {
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important; }

:host([color="orange"]),
:host([priority="warning"]) {
  --pfe-badge--base--bg:                  var(--pfe-theme--color--feedback--important, #d73401);
  --pfe-badge--base--color:                     var(--pfe-theme--color--text--on-dark, #fff);
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important;
  font-weight: var(--pfe-theme--font-weight--semi-bold, 600); }
  :host([color="orange"]) ::slotted(*),
  :host([priority="warning"]) ::slotted(*) {
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important; }

:host([color="red"]),
:host([priority="critical"]) {
  --pfe-badge--base--bg:                   var(--pfe-theme--color--feedback--critical, #990000);
  --pfe-badge--base--color:                     var(--pfe-theme--color--text--on-dark, #fff);
  background-color: var(--pfe-badge--base--bg) !important;
  color: var(--pfe-badge--base--color) !important;
  font-weight: var(--pfe-theme--font-weight--semi-bold, 600); }
  :host([color="red"]) ::slotted(*),
  :host([priority="critical"]) ::slotted(*) {
    background-color: var(--pfe-badge--base--bg) !important;
    color: var(--pfe-badge--base--color) !important; }

:host([hidden]) {
  display: none; }</style>
<slot></slot>`;
  }

  static get tag() {
    return "pfe-badge";
  }

  get templateUrl() {
    return "pfe-badge.html";
  }

  get styleUrl() {
    return "pfe-badge.scss";
  }

  // static get observedAttributes() {
  //   return [];
  // }

  constructor() {
    super(PfeBadge);
  }

  // connectedCallback() {
  //   super.connectedCallback();
  // }

  // disconnectedCallback() {}

  // attributeChangedCallback(attr, oldValue, newValue) {}
}

PFElement.create(PfeBadge);

export default PfeBadge;
